.appWrapper {
    display: grid;
    grid-template-areas:
  "head head"
  "nav content";
    grid-template-rows: 80px 100vh;
    grid-template-columns: 180px 10fr;
    font-family: 'Courier', monospace;
    /*height: 100%;*/
}

.appWrapperContent {
    position: relative;
    grid-area: content;
    background-color: #bd4c4c;
    padding-bottom: 40px;

}

@media only screen and (max-width: 480px) and (min-width: 0px) {
    .appWrapper {
        display: grid;
        grid-template-areas:
            "head head"
            "content content";
        grid-template-columns: 1fr 1fr;
        overflow-x: hidden;
        position: relative;
    }

/*    .appWrapperContent {*/
/*        padding: 20px;*/
/*    }*/
}